export default {
	data() {
		return {
			flag:''
		};
	},
	onLoad(obj) {
		if(obj.name =='self' ){
			this.flag = 'self'
			uni.setNavigationBarTitle({
				title:'我的自提地址'
			})
		}else{
			this.flag = 'address'
			uni.setNavigationBarTitle({
				title:'我的配送地址'
			})
		}
	},
	computed:{
		info(){
			return this.flag == 'self'? '没有自提地址，快去添加':'没有配送地址，快去添加'
		},
		btn(){
			return this.flag == 'self'? '新增自提地址':'新增配送地址'
		}
	},
	methods:{
		addAddress(){
				uni.navigateTo({
					url:'/pages/newAddress/newAddress?name='+this.flag
				})
		}
	}
}
